import React, { useRef } from "react";
import X from "./media/X.jpg";
import O from "./media/O.jpg";
import blank from "./media/blank.jpg";

export default function Cell({ mark, playerMove }) {
  const cellRef = useRef(null);

  let src;
  if (mark === 1) {
    src = X;
  } else if (mark === -1) {
    src = O;
  } else {
    src = blank;
  }

  return (
    <img
      src={src}
      ref={cellRef}
      className="cell"
      alt="cell"
      onClick={playerMove}
    />
  );
}
