import "./App.css";
import Cell from "./Cell";
import React, { useState } from "react";
import * as tf from "@tensorflow/tfjs";

const initialState = [0, 0, 0, 0, 0, 0, 0, 0, 0];
let boardCopy = initialState;

const MODEL_PATH = process.env.PUBLIC_URL + "/model/ttt_model.json";

export default function App() {
  const playerMark = -1;
  const aiMark = 1;
  const [board, setBoard] = useState([0, 0, 0, 0, 0, 0, 0, 0, 0]);

  const sortIndices = (array) => {
    let newArray = [...array];
    newArray = newArray.map((v, i) => {
      return { index: i, value: v };
    });
    newArray.sort((a, b) => b.value - a.value);
    newArray = newArray.map((e) => e.index);
    return newArray;
  };

  const makeMove = (index, mark) => {
    if (boardCopy[index] === 0) {
      boardCopy = boardCopy.map((cell, i) => (i === index ? mark : cell));
      setBoard(boardCopy);
    }
  };

  const playerMove = (index) => () => {
    makeMove(index, playerMark);
    aiMove();
  };

  const aiMove = () => {
    tf.tidy(() => {
      tf.loadLayersModel(MODEL_PATH).then((model) => {
        const tensor = tf.tensor(boardCopy);
        const tensors = tf.stack([tensor]);
        const result = model.predict(tensors);
        result.array().then((res) => {
          const array = sortIndices(res[0]);
          let i = 0;
          while (boardCopy[array[i]] !== 0 && i < 10) {
            i++;
          }
          if (i !== 9) {
            makeMove(array[i], aiMark);
          }
        });
      });
    });
  };

  return (
    <div className="board">
      {board.map((mark, i) => (
        <Cell key={i} mark={mark} playerMove={playerMove(i)} />
      ))}
    </div>
  );
}
